import Http from './http'
import Axios from 'axios'
/**
 * //
 * @param {get} params
 * @data {post} data
 * @data {put} data
 * @data {delete} data
 */

// 
export function getCity() {
  return new Promise((resolve, rejejct) => {
    Axios.get('/Dictionary')
      .then(respone => {
        resolve(respone.data)
      })
      .catch(error => {
        rejejct(error)
      })
  })
}

// login
export function login(data) {
  return Http({
    url: 'admin/login',
    method: 'post',
    data
  })
}
// getInfo

export function getUserInfo(params) {
  return Http({
    url: 'admin/getInfo',
    method: 'get',
    params
  })
}

// FLM
export function getFlmHouse(params) {
  return Http({
    url: 'admin/getFLM',
    method: 'get',
    params
  })
}
