<template>
  <div>
    <base-header />
    <base-slider />
    <div class="content_box" :class="{'content-isCollapse': isCollapse}">
      <base-breadcrumb />
      <div class="allInfo">
        <keep-alive :include="tagsList">
          <router-view />
        </keep-alive>
      </div>

      <div class="footer"> FBI WARING @ 2019</div>
    </div>
  </div>
</template>

<script>
import './Slider/slider.css'
import BaseHeader from './Header'
import BaseSlider from './Slider'
import BaseBreadcrumb from '@/component/BaseBreadcrumb'
import bus from '@/common/bus'
// 
export default {
  components: { BaseHeader, BaseSlider, BaseBreadcrumb },
  data() {
    return {
      isCollapse: false,
      tagsList: []
    }
  },
  mounted() {
    bus.$on('collapse', msg => {
      this.isCollapse = msg
    })
  }
}
</script>

<style lang="scss" scoped>
.content_box {
  position: absolute;
  left: 250px;
  top: 70px;
  right: 0;
  bottom: 0;
  overflow: hidden;
  overflow-y: scroll;
  transition: left 0.5s ease-in;
  background-color: #f0f0f0;
  .allInfo {
    width: auto;
    min-height: 100%;
    margin-top: 10px;
    margin-left: 10px;
    background-color: #fff;
    border-radius: 10px;
    box-sizing: border-box;
  }
}
.content-isCollapse {
  left: 65px;
}
.footer{
  background-color:#ccc;
  margin-top: 20px;
  font-size: 16px;
  line-height: 60px;
  text-align: center;
  width: 100%;
}
</style>