// import { Message } from 'element-ui'
const state = {
  label: '',
  id: ''
}
const actions = {
  searchPart({ commit }, data) {
    console.log(data)
    return new Promise(resolve => {
      const { label, id } = data
      commit('SET_ID', id)
      commit('SET_LABEL', label)
      resolve(data)
    })
  }
}

const mutations = {
  SET_ID: (state, id) => {
    state.id = id
  },
  SET_LABEL: (state, label) => {
    state.label = label
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}
